import type { Invoke, YodaInterface } from '@/types/bridge-invoke';
import { error } from './console';
import type { SetClientLogTypes } from '@/types/bridge';

export { SetClientLogTypes } from '@/types/bridge';
export type { Invoke, YodaInterface } from '@/types/bridge-invoke';

let invoke!: Invoke;

export const isInYoda = () => {
    // @ts-ignore
    return typeof window?.__yodaBridge__?.invoke === 'function';
};

/**
 * 是否支持桥接 canIUse
 */
const isSupportBridge = async (name: string, namespace = 'tool') => {
    if (typeof invoke === 'undefined') return false;
    const res = await invoke('tool.canIUse', {
        namespace,
        name
    });
    return (res as any)?.canUse as boolean || false;
};

/**
 * 是否支持桥接上报埋点接口
 */
export const isSupportBridgeLog = () => {
    return isSupportBridge('setClientLog');
};

/**
 * 传入 invoke 方法
 */
export const postInvoke = (invokeOrYoda: YodaInterface | Invoke) => {
    if (typeof invokeOrYoda === 'function') {
        invoke = invokeOrYoda;
    } else {
        invoke = invokeOrYoda.invoke;
    }
};

/**
 * 发送业务事件
 */
export const setClientLog = async (type: SetClientLogTypes, data: unknown) => {
    try {
        return await invoke('tool.setClientLog', { type, data });
    } catch (err) {
        error(
            '[error 206]', `yoda.tool.setClientLog() 报错: ${(err as Error).message}`,
        );
    }
};

