/**
 * 纯端内桥接版本，最小化包体积
 */
import type { BasePlugin, BasePluginConstructor } from '@/plugins/base';
import type { BridgeWeblogInitParams, UrlObj, PageTypeKey } from '@/types/configuration';
import type {
    EventType as IEventType,
    EventOptions as IEventOptions,
} from '@/types/event';
import {
    getBridgeNormalIncrementId,
    getBridgeCustomIncrementId,
} from '@/util/increment-id';
import cookieUtils, { getCookie } from '@/util/cookie';
import { oldApiTransformer } from '@/util/history-polyfill';
import { logFactory } from './log';
import yodaUtils, { setClientLog, postInvoke } from '@/util/yodav2';
import type { SetClientLogTypes } from '@/util/yodav2';
import sendData from '@/io/xhr';
import UrlPackage from '../browser/url-package';
import {
    pageUrlValidate,
    getResolution,
    supportsPushState,
    getPositiveInteger,
    checkSupportCloseCoPage,
    stringifyParams
} from '@/util';
import { error } from '@/util/console';
import { isKDSNative } from '@/util/ua';
import { invoke, version } from '@yoda/bridge';

postInvoke(invoke);
/**
 * 判断当前yoda的状态
 * INIT: 初始状态
 * READY：可用，此时上报的埋点会以桥接方式上报
 * DISABLED：不可用，此时上报的埋点会以 http 方式上报
 * CHECKING：检查是否可用中，此时上报的埋点都会推入等待队列
 */
 enum YodaStatus {
    INIT = -1,
    CHECKING = 0,
    READY = 1,
    DISABLED = 2,
}

let pluginIndex = 0;

export default class Weblogger {
    static __instance: Weblogger;
    version = '__VERSION__';
    currentUrlPackage!: UrlPackage & { category?: string };
    referUrlPackage?: UrlPackage;
    commonPackage: CommonPackage;
    logConfig: BridgeWeblogInitParams[0];
    baseOption?: BridgeWeblogInitParams[1];
    // 插件
    plugins: {
        [key: string]: BasePlugin | any;
    } = {};
    // 记录页面是否已发送过 enter pv;
    entered: String = '';
    constructor(configOption: BridgeWeblogInitParams[0], baseOption?: BridgeWeblogInitParams[1]) {
        this.logConfig = configOption;
        this.baseOption = baseOption;
        this.commonPackage = new CommonPackage(baseOption);
        this.updateCurrentUrlPackage();
        this.addPlugins();
        if (!Weblogger.__instance) {
            Weblogger.__instance = this;
        }
        console.warn('[webLogger] 您正在使用的是',  '【纯桥接上报】的 js 包，端外打开不会上报埋点。');
    }

    get Utils() {
        return {
            yoda: yodaUtils,
            cookie: cookieUtils,
            ua: {
                getDefaultKpn: () => getCookie('kpn'),
                supportsPushState,
            },
            io: {
                sendData,
            },
        }
    }
    get sessionId() {
        return getCookie('session_id') || getCookie('sid');
    }
    /**
     * 初始化插件
     * @param plugins
     */
    addPlugins() {
        if (!this.logConfig.plugins || !this.logConfig.plugins.length) return;
        // 类似 webpack plugins 的实例数组
        this.logConfig.plugins.forEach((plugin: any) => {
            if (typeof plugin === 'object' && typeof plugin.apply === 'function') {
                this.addPluginInstance(plugin);
            }
        });
    }
    /**
     * 加载插件实例
     */
    addPluginInstance(pluginInstance: any) {
        if (pluginInstance) {
            const key = pluginInstance.key || pluginInstance.constructor && pluginInstance.constructor.key || `plugin_auto_key_${pluginIndex++}`;
            if (typeof pluginInstance.apply === 'function'
                && (!pluginInstance.weblog || pluginInstance.weblog !== this)
            ) {
                pluginInstance.apply(this);
            }
            this.plugins[key] = pluginInstance;
        }
    }
    /**
     * 加载插件构造函数
     * @param plugin
     * @param pluginOptions
     */
    plug(plugin: BasePluginConstructor, pluginOptions?: any) {
        if (this.plugins[plugin.key]) {
            return error('[code 301]', `${plugin.key} 插件重复加载！`)
        }
        this.addPluginInstance(new plugin(this, pluginOptions));
    }

    /**
     * 收集上报
     * @param type
     * @param options
     * @returns
     */
     collect<T extends IEventType>(type: T, options: IEventOptions<T>): void;
     collect<T extends IEventType>(type: T, name: string, params?: any, contentPackage?: { [keys: string]: any } | string): void;
     collect<T extends IEventType>(
         type: T,
         name: string | IEventOptions<T>,
         params?: any,
         contentPackage?: { [keys: string]: any } | string,
     ) {
        const [logType, logParams] = oldApiTransformer(type, name, params, contentPackage);
        return this.send(logType, logParams, false);
    }

    /**
     * 立即上报
     * @param type
     * @param options
     * @returns
     */
     sendImmediately<T extends IEventType>(type: T, options: IEventOptions<T>): void;
     sendImmediately<T extends IEventType>(type: T, name: string, params?: any, contentPackage?: { [keys: string]: any } | string): void;
     sendImmediately<T extends IEventType>(
         type: T,
         name: string | IEventOptions<T>,
         params?: any,
         contentPackage?: { [keys: string]: any } | string,
     ) {
        const [logType, logParams] = oldApiTransformer(type, name, params, contentPackage);
        return this.send(logType, logParams, true);
    }
    sendByBridge<T extends IEventType>(
        eventType: T,
        eventOptions: IEventOptions<T>,
        immediately?: boolean,
    ) {
        const { disablePV: configDisablePV, biz } = this.logConfig;
        let { contentPackage, disablePV: eventDisablePV, bridgeFields = {}, urlPage } = eventOptions as IEventOptions<'CLICK'>;
        const disablePV = configDisablePV || eventDisablePV;
        if (contentPackage) {
            contentPackage = typeof contentPackage === 'string' ? contentPackage : JSON.stringify(contentPackage);
        }
        let setClientLogType: SetClientLogTypes = 'addTaskEvent';
        const isPVEvent = eventType === 'PV';
        if (isPVEvent) {
            if (disablePV) return;
            // bridge PV 仅支持 enter 和 visible
            const supportPVTypes = ['enter', 'visible'];
            let { page, type, params, coPage, category } = eventOptions as IEventOptions<'PV'>;
            // 半屏离开事件，直接调用新的方法
            if (type === 'leave' && this.currentUrlPackage.coPage && checkSupportCloseCoPage()) {
                setClientLog('closeCoPage', {
                    ...eventOptions,
                    ...this.currentUrlPackage,
                });
                return;
            }
            if (!type) (eventOptions as IEventOptions<'PV'>).type = type = 'enter';
            if (supportPVTypes.indexOf(type) === -1) {
                return;
            }
            // enter 则强行重新生成新的 currentUrlPackage，否则局部更新
            if (type === 'enter') {
                // params 和 page 的处理和 hybrid 保持一致，兼容 autopv 插件发送 pv 时在插件内部已经修改 currentUrlPackage 内容
                params = !page ? this.currentUrlPackage.params : params;
                page = page || this.currentUrlPackage.page;
                this.updateCurrentUrlPackage({ page, params, force: true });
                if (coPage) {
                    this.currentUrlPackage.coPage = coPage;
                }
                if (category) {
                    this.currentUrlPackage.category = category;
                }
                this.entered = page;
            } else if (page === this.entered) {
                this.currentUrlPackage.update(page, params);
            }
            // 对于上报到v2，page为url的埋点，给予提示
            if (!pageUrlValidate(this.currentUrlPackage.page)) return;
            setClientLogType = 'setCurrentPage';
        }
        const coPage = !!this.currentUrlPackage.coPage;
        const urlPackage = urlPage?.page && !isPVEvent ? {
            page: urlPage.page,
            identity: urlPage.identity,
            params: stringifyParams(urlPage.params),
            page_type: this.currentUrlPackage.page_type
        } : this.currentUrlPackage.toJSON();
        const log = logFactory(eventType, {
            biz,
            ...eventOptions,
            contentPackage,
            currentUrlPackage: disablePV ? undefined : urlPackage,
        } as any); // TODO: 暂时 any
        const isCustomType = ['CUSTOM', 'RADAR', 'CUSTOM_EVENT'].indexOf(eventType) !== -1;
        // 保持 h5ExtraAttr 来源统一
        this.commonPackage.increaseH5SeqId(isCustomType);
        // 设置 log 公共属性
        const { service_name, sub_biz, need_encrypt } = this.baseOption || {};
        // TODO: 桥接上报不需要 common_package 信息，可以优化
        log.h5ExtraAttr = JSON.stringify(this.commonPackage.getH5ExtraAttr({ coPage }));
        log.realtime = !!immediately;
        log.serviceName = service_name || '';
        log.subBiz = sub_biz || '';
        log.needEncrypt = need_encrypt || false;
        log.container = isKDSNative() ? 'REACT_NATIVE' : 'H5';
        if (eventType === 'SHOW') {
            setClientLogType = 'addElementShowEvent';
        } else if (isCustomType) {
            // 兼容业务一些迁移行为，自动把 custom_stat_event 转为 custom_event
            setClientLogType = (eventType === 'CUSTOM_EVENT' || this.logConfig.customStatToCustom) ? 'addCustomEvent' : 'addCustomStatEvent';
        }
        setClientLog(setClientLogType, {
            ...bridgeFields,
            ...log,
        });
    }
    send<T extends IEventType>(eventType: T, eventOptions: IEventOptions<T> = {}, immediately?: boolean) {
        eventType = eventType.toUpperCase() as T;
        this.sendByBridge(eventType, eventOptions, immediately);
    }
    updateCurrentUrlPackage(url: string | UrlObj = location?.href || '', type: PageTypeKey = 'web') {
        if (typeof url === 'object' && !url.force && this.currentUrlPackage) {
            const { page = this.currentUrlPackage.page, params = {} } = url;
            if (!page || page === this.currentUrlPackage.page) {
                const isPageEntered = this.entered === page;
                if (!isPageEntered) {
                    this.currentUrlPackage.update(page, params);
                    return;
                }
                // 客户端内直接使用 updateCurrentUrlPackage 更改页面参数是无法成功的
                // 客户端只认 pv 上报时候带上去的参数，因此此处发送一个visible类型的pv，改变客户端维护的页面参数
                this.send('PV', {
                    type: 'visible',
                    page,
                    params,
                }, true);
                return;
            }
        }
        this.currentUrlPackage = new UrlPackage(url, type, this.logConfig.urlMap);
        if (this.logConfig.attachUrl) this.currentUrlPackage.attachUrl();
    }

    /**
     * 更新 refer url 信息
     * @param url
     * @param type
     */
    updateReferUrlPackage(url: string | UrlObj | UrlPackage = this.currentUrlPackage, type: PageTypeKey = 'web') {
        if (url instanceof UrlPackage) {
            this.referUrlPackage = url;
        } else {
            this.referUrlPackage = new UrlPackage(url, type, this.logConfig.urlMap);
        }
    }
    updateCommonPackage(options: BridgeWeblogInitParams[1]) {
        this.commonPackage.update(options);
    }
    flush() {}
    on() {}
    emit() {}
}

class CommonPackage {
    /**
     * 容器类型
     */
    container?: 'REACT_NATIVE' | 'H5';
    /**
     * 中台服务名称，比如积木、im sdk等，如果业务log未使用中台服务，该字段置空
     */
    service_name?: string;
    /**
     * 子业务场景
     */
    sub_biz?: string;
    /**
     * 是否需要加密
     */
    need_encrypt?: boolean;
    h5_extra_attr: {[key: string]: any} = {
        // SDK 版本信息
        sdk_name: '__SDK_NAME__',
        sdk_version: '__VERSION__',
        sdk_bundle: '__SDK_BUNDLE__',
        host_product: getCookie('kpn'),
        resolution: getResolution(),
        domain: window.location.origin,
        app_version_name: getCookie('appver') || '',
        bridge_info: version || true,
    };
    app_package?: {
        version_name: string;
        container?: 'REACT_NATIVE' | 'H5'
    };

    constructor(option?: BridgeWeblogInitParams[1]) {
        this.update(option);
    }

    getVersionName() {
        return this.app_package?.version_name;
    }

    update(option?: BridgeWeblogInitParams[1]) {
        Object.assign(this.h5_extra_attr, option?.h5_extra_attr);
        this.container = option?.container;
        this.service_name = option?.service_name;
        this.sub_biz = option?.sub_biz;
        this.need_encrypt = option?.need_encrypt;
        this.app_package = {
            version_name: this.h5_extra_attr.app_version_name,
            container: isKDSNative() ? 'REACT_NATIVE' : 'H5'
        }
    }

    // 用于统计桥接丢失率
    increaseH5SeqId(isCustom?: Boolean) {
        this.h5_extra_attr.client_timestamp = getPositiveInteger(Date.now());
        this.h5_extra_attr.seq_id = isCustom ? getBridgeCustomIncrementId() : getBridgeNormalIncrementId();
    }

    getH5ExtraAttr(extraInfo?: {[key: string]: any}) {
        return Object.assign({}, this.h5_extra_attr, extraInfo);
    }
}
